@import (less) 'color.less';
@fontSizeTitle: 0.875em;
.panel {
    box-shadow: none;
    background-color:transparent;
    &.nbp-panel-disabled {
      opacity: 0.5;
      .panel-heading:hover, [role=button]:hover {
        cursor: not-allowed !important;
      }
    }
    .panel-open {
        background-color: @dark_white !important;
        border: 1px !important;
        border-color: @gray;
    }
    .panel-heading {
        padding: 1em 1.250em 1em 1.250em !important;
        height: 50px;
        width: 300px;
        box-shadow: none;
        &:hover {
            cursor: pointer;
        }
        .title{
          display: inline-block;
          max-width: 90%;
          white-space: nowrap;
          overflow:hidden;
          text-overflow: ellipsis;
        }
    }
    .panel-collapse {
        .panel-body {
            border: 1px !important;
            box-shadow: none;
            background-color: @dark_white !important;
        }
        max-height: 0;
        transition: max-height 0.15s ease-out;
        overflow: hidden;
        &.in {
            max-height: 1000em;
            transition: max-height 0.25s ease-in;
        }
    }
}

:host-context(.contrasto-on) {
    .panel-body {
        background-color: @white !important;
        border-top: none !important; // border-right: 2px solid @black !important;
        // border-bottom: 2px solid @black !important;
        // border-left: 2px solid @black !important;
        color: @black;
    }
}

.panel.nbp-panel-showmore {
    border: 0px !important;
    border-color: @white;
    .panel-heading {
        border: 1px !important; // border-radius: 2px 2px 0px 0px !important;
        color: @green;
        border-color: @gray;
        background-color: transparent;
        &.panel-heading-round {
            //   border-radius: 25px !important;
        }
        .panel-title {
            color: green;
            font-weight: bold;
            font-size: 0.750em !important;
            text-transform: uppercase;
        }
        .accordion-arrow {
            color: black;
            position: relative;
            font-size: 26px !important;
            margin-top: 1.156em;
        }
        .stringa {
            color: @white;
            font-size: 0.750em !important;
            margin-right: 1.875em;
            font-weight: bold;
            font-family: arial;
        }
        h4 {
            margin: 0;
            font-size: 1em;
        }
    }
    .panel-body {
        border-radius: 0px 0px 2px 2px !important;
    }
    .panelTitle {
        font-size: @fontSizeTitle;
        font-weight: bold; //   text-transform: uppercse;
    }
    .accordionIcon {
        font-size: 32px;
        position: absolute;
        top: 9px;
    }
    a {
        text-decoration: none !important;
        margin-top: -5px;
        .icon:before {
            font-size: 26px;
        }
    }
    >.panel-heading {
        background-color: transparent !important;
        position: relative;
        & .accordion-icon {
            .accordionIcon
        }
        >.panel-title {
            .panelTitle;
            >.title {
                padding: 2px 2px 2px 0px;
            }
            .accordion-title-icon {
                padding: 2px 2px 2px 42px !important;
                margin-bottom: -4px;
            }
        }
        >.panel-open {
            background-color: @lighter_green !important;
        }
    }
}

:host-context(.contrasto-on) {
    .panel.nbp-panel-showmore {
        .panel-open {
            background-color: @white !important;
            color: @black; // border: 1px solid @black !important;
            .panel-title {
                color: @black;
            }
            .stringa {
                color: @black;
            }
            .accordion-arrow {
                color: @black;
            }
        }
        .panel-heading {
            background-color: @white !important; // &:hover {
            //   background-color: @white !important;
            //   color: @black;
            //   border: 1px solid @black !important;
            //   .panel-title {
            //     color: @black;
            //   }
            //   .stringa {
            //     color:@black;
            //   }
            //   .accordion-arrow {
            //     color:@black;
            //   }
            // }
            .panel-title {
                color: @black;
            }
            .accordion-arrow {
                color: @black;
            }
            .stringa {
                color: @black;
            }
        }
        .accordion-green-header {
            >.panel-heading {
                background-color: @black !important;
                &:hover {
                    background-color: @white !important;
                    color: @black;
                    border: 1px solid @black;
                }
            }
            >.panel-open {
                background-color: @white !important;
                color: @black;
                border: 1px solid @black !important;
            }
        }
    }
}

